@font-face {
  font-family: 'Halvetica Neue';
  src: 'url(/public/fonts/Helvetica LT 63 Medium Extended.ttf)';
  font-weight: 500;
}
@font-face {
  font-family: 'Halvetica Neue';
  src: 'url(/public/fonts/Helvetica LT 83 Heavy Extended.ttf)';
  font-weight: bold;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Halvetica Neue';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
